const Contact = () =>{
    return(
        <div id="contact" className="contact-cont">
            <section className="contact-flex">
                <section className="contact">
                    <h1><span>/Contact</span></h1>
                    <div className="p-flex">
                        <p>I would love to hear from you, send me a message.</p>
                    </div>
                    <section>
                        <div>
                            <fieldset>
                                <form action="/" method="POST" autoComplete="off" spellCheck="false">
                                    <input id="input" type="text" name="email" placeholder="Email"></input>
                                    <textarea name="message" id="texta" placeholder="Message"></textarea>
                                    <div>
                                        <button type="submit">Send Message</button>
                                    </div>
                                </form>
                            </fieldset>
                        </div>
                        <div className="beside-field">
                            <img src="bye.svg" alt="" />
                        </div>
                    </section>
                </section>
            </section>
        </div>
    );
}

export default Contact;